html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: black;
}

img {
  width: 100%;
  padding: 0;
}

body {
  font-family: "Josefin Sans", sans-serif;
  line-height: 1.6rem;
  background-color: white;
}

.contenedor {
  max-width: 110rem;
  width: 80%;
  margin: 0 auto;
}

.header {
  position: relative;
  background-image: url(/build/img/desktop/image-hero.webp);
  width: auto;
  max-height: auto;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.header::before {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.contenedor {
  position: relative;
}

.barra-navegacion {
  padding-top: 1.5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: white;
}
.barra-navegacion .cerrar {
  width: 3rem;
}
@media (min-width: 1024px) {
  .barra-navegacion .cerrar {
    display: none;
  }
}

.logotipo {
  font-size: 3rem;
  font-family: "Alata", sans-serif;
  margin-top: 0;
}

.navegacion {
  position: absolute;
  top: 12rem;
  left: auto;
  border: 1px solid white;
  padding: 1rem;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.2s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 1024px) {
  .navegacion {
    position: inherit;
    border: none;
    top: none;
    right: none;
    padding: 0;
    visibility: visible;
    opacity: 1;
  }
}
.navegacion nav {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .navegacion nav {
    flex-direction: row;
  }
}
.navegacion nav a {
  margin-bottom: 1.5rem;
  color: white;
  font-size: 1.8rem;
  font-weight: 900;
  line-height: 3rem;
  position: relative;
}
.navegacion nav a:hover {
  color: #8c8c8c;
}
.navegacion nav a:before {
  content: "";
  display: block;
  margin: 0;
  width: 0;
  height: 3px;
  background: white;
  position: absolute;
  bottom: 0;
  transition: all 0.3s;
}
.navegacion nav a:hover:before {
  width: 100%;
}
@media (min-width: 1024px) {
  .navegacion nav a {
    margin-right: 1.5rem;
  }
}
.navegacion nav a:before {
  left: 50%;
}
.navegacion nav a:hover:before {
  left: 0;
}

.mostrar {
  visibility: visible;
  opacity: 1;
}

.new-image {
  background-image: url(/images/icon-close.svg);
}

.frase {
  padding-bottom: 5rem;
  padding-top: 28rem;
}
@media (min-width: 1024px) {
  .frase {
    padding-top: 20rem;
  }
}
.frase .contenido-frase {
  font-size: 2.5rem;
  text-transform: uppercase;
  max-width: auto;
  max-height: auto;
  color: white;
  border: 4px solid white;
  line-height: 4rem;
}
@media (min-width: 768px) {
  .frase .contenido-frase {
    font-size: 8rem;
    max-width: 80rem;
    max-height: 40rem;
    line-height: 10rem;
  }
}
.frase .contenido-frase p {
  margin: 0;
  padding: 2rem;
  max-width: auto;
  font-size: 3.6rem;
  line-height: 6rem;
}
@media (min-width: 768px) {
  .frase .contenido-frase p {
    max-width: inherit;
    font-size: 7rem;
    line-height: 7rem;
  }
}

main {
  padding: 7rem 0 7rem 0;
}
@media (min-width: 1024px) {
  main .encimados {
    display: flex;
    justify-content: center;
  }
}
@media (min-width: 1024px) {
  main .encimados img {
    width: 80%;
    height: 55rem;
    object-fit: cover;
  }
}

.contenido-interactivo {
  background-color: white;
  max-width: 50rem;
  height: 20rem;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .contenido-interactivo {
    position: absolute;
    padding: 5rem;
    top: 260px;
    left: 550px;
    width: 50rem;
    height: 40rem;
  }
}
.contenido-interactivo .titulo-interactivo {
  font-size: 2rem;
  text-transform: uppercase;
  line-height: 4rem;
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-interactivo .titulo-interactivo {
    text-align: left;
    font-size: 3rem;
  }
}
@media (min-width: 1024px) {
  .contenido-interactivo .titulo-interactivo {
    font-size: 4rem;
  }
}
.contenido-interactivo .parrafo-interactivo {
  font-size: 2rem;
  line-height: 2.5rem;
  font-weight: 700;
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-interactivo .parrafo-interactivo {
    text-align: left;
  }
}

.dark-mode {
  width: 3rem;
}
.dark-mode img {
  margin-top: 3rem;
  filter: invert(100%);
}

.oscuro-agregado {
  transition: all 0.3s ease-in;
  background-color: #212121;
}
.oscuro-agregado .creacion, .oscuro-agregado .boton-all {
  color: white;
}
.oscuro-agregado .dark-mode {
  transition: all 0.3s ease-in;
  filter: invert(80%);
}
.oscuro-agregado .boton-down {
  border: 1px solid white;
  color: white;
}
.oscuro-agregado .contenido-interactivo {
  transition: all 0.3s ease-in;
  background-color: #212121;
}
.oscuro-agregado .contenido-interactivo .titulo-interactivo, .oscuro-agregado .contenido-interactivo .parrafo-interactivo {
  color: white;
}

.contenedor-galeria {
  display: none;
}
@media (min-width: 768px) {
  .contenedor-galeria {
    padding-bottom: 4rem;
    display: block;
  }
}

.creacion, .creacion-mobile {
  font-size: 3rem;
  font-weight: 700;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .creacion, .creacion-mobile {
    font-size: 4rem;
  }
}

.title-galeria {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.boton-all {
  border: 1px solid #333333;
  font-size: 2rem;
  padding: 1rem 3rem;
  transition: all 0.3s ease;
  text-transform: uppercase;
  font-weight: 700;
}
.boton-all:hover {
  background-color: black;
  color: white;
  transform: scale(1.2);
}

@media (min-width: 768px) {
  .imagenes-contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .imagenes-contenido {
    grid-template-columns: repeat(4, 1fr);
  }
}
.imagenes-contenido .imagen-cuadricula {
  position: relative;
  width: 100%;
  display: flex;
}
.imagenes-contenido .info {
  position: absolute;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  background: rgba(0, 0, 0, 0.466);
  height: 100%;
  width: 100%;
  opacity: 0;
}
.imagenes-contenido p {
  color: white;
  font-size: 3rem;
  text-transform: uppercase;
  line-height: 3rem;
  font-weight: bold;
  max-width: 17rem;
  margin-left: 2rem;
}

.imagen-cuadricula:hover .info, p {
  opacity: 1;
  transition: 0.3s all ease-in;
}

.mobile-design {
  margin-bottom: 5rem;
  margin-top: 10rem;
}
@media (min-width: 768px) {
  .mobile-design {
    display: none;
  }
}
.mobile-design .cuadricula-mobile {
  position: relative;
  display: flex;
  margin-bottom: 2rem;
}
.mobile-design .info-mobile {
  position: absolute;
  background: rgba(0, 0, 0, 0.527);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
  transition: 0.4s all ease-in-out;
  opacity: 0;
}
.mobile-design .info-mobile p {
  font-size: 2rem;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  margin-left: 2rem;
}
.mobile-design .info-mobile:hover, .mobile-design p {
  opacity: 1;
}
.mobile-design .creacion-mobile {
  font-size: 2.5rem;
  text-align: center;
}
.mobile-design .boton {
  width: 10rem;
  margin: 0 auto;
}

.boton-down {
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 700;
  border: 1px solid black;
  padding: 0.5rem 1rem;
  text-align: center;
  transition: all 0.3s ease-in-out;
  max-width: 10rem;
}
.boton-down:hover {
  background-color: black;
  color: white;
  transform: scale(1.2);
}

.footer {
  background-color: black;
}
.footer .footer-seccion {
  display: flex;
  flex-direction: column;
  padding-top: 5rem;
  padding-bottom: 4rem;
}
@media (min-width: 768px) {
  .footer .footer-seccion {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer .first-seccion .image-logo {
  display: flex;
  justify-content: center;
}
.footer .first-seccion .image-logo img {
  align-items: center;
  width: 20rem;
}
.footer .iconos .redes {
  display: flex;
  max-width: 20rem;
  margin: 0 auto;
}
.footer .iconos .redes img {
  margin-left: 2rem;
  transition: all 0.3s ease-in-out;
  width: auto;
  margin: 0 auto;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .footer .iconos .redes img {
    margin: 0 1rem;
    padding-top: 0;
  }
}
.footer .iconos .redes img:hover {
  transform: scale(1.2);
  cursor: pointer;
}
.footer .iconos p {
  color: white;
  font-size: 1.5rem;
  margin-top: 5rem;
  text-align: center;
}

.navegacion-footer .nav-separacion {
  display: flex;
  flex-direction: column;
  max-width: 8rem;
  margin: 0 auto;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .navegacion-footer .nav-separacion {
    flex-direction: row;
    max-width: auto;
    margin: 4rem 0;
  }
}
.navegacion-footer .nav-separacion a {
  margin-right: 1.5rem;
  color: white;
  font-size: 1.4rem;
  font-weight: 900;
  line-height: 3rem;
  position: relative;
  text-align: center;
}
.navegacion-footer .nav-separacion a:hover {
  color: #8c8c8c;
}
.navegacion-footer .nav-separacion a:before {
  content: "";
  display: block;
  margin: 0;
  width: 0;
  height: 3px;
  background: white;
  position: absolute;
  bottom: 0;
  transition: all 0.3s;
}
.navegacion-footer .nav-separacion a:hover:before {
  width: 100%;
}